
@mixin blue-btn {
    box-sizing: border-box;
    display: inline-block;
    border-radius: 4px;
    color: #fff;
    border: 1px solid #fff;
    font-weight: 700;
    background-color: $blue;
    transition: all 0.3s ease;

    &:hover {
        cursor: pointer;
        border: 1px solid $blue;
        color: $blue;
        background: #fff;
        text-decoration: none;
    }
    
}

@mixin orange-btn {
    box-sizing: border-box;
    display: inline-block;

    font-weight: 700;
    color: #000000;
    
    border-radius: 5px;
    text-shadow: 0 0 24px rgba(255, 255, 255, 0.91);
    background-image: linear-gradient(to bottom, #ffe19b, #febf62);
    box-shadow: 0 0 0 0 #ffffff, inset 0 4px 2px 0 rgba(255, 255, 255, 0.5);
    border: solid 2px #c88b31;
    transition: all 0.4s ease;

    &:hover {
        cursor: pointer;
        background-image: linear-gradient(to top, #ffe19b, #febf62);
        text-decoration: none;
        color: #000000;
    }
}

@mixin highlight {
    border-radius: 1em 0 1em 0;
    background-image: 
    linear-gradient(-100deg, rgba(250,247,133,0.3), rgba(250,247,133,0.7) 98%, rgba(250,247,133,0.1));
}

@mixin with-arrow {
    position: relative;

    &::after {
        content: '';
        position: absolute;
        
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
    }
}


@mixin shadow {
    box-shadow: 0 9px 30px 0 rgba(65, 84, 121, 0.15);
}
